* {
    outline: none;
    box-sizing: border-box;
}

body {
    margin: 0px;
    user-select: none;
    -webkit-user-select: none;
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none;
    background-color: map-get($colors, primary);
    
    a {
        text-decoration: none; 
    }

    .center {
        margin: 0 auto;
    }

    .header {
        min-height: 50px;
    }

    .structure {
        width: 100%;
        max-width: 480px;
        margin: 0 auto;
    }

    .bottom-tab {
        -webkit-box-shadow: 0px -5px 4px 0px alphaColor(black, 0.05); 
        box-shadow: 0px -5px 4px 0px alphaColor(black, 0.05);
    }

    .app-drawer {
        &.stay {
            .ant-drawer-body {
                padding: 0px;
            }
        }

        .ant-drawer-header {
            border-bottom-width: 0px;
        }

        .ant-drawer-content-wrapper {
            width: 100% !important;
            max-width: 480px;
            margin: 0 auto;
            position: initial;
        }
      
        .ant-drawer-body {
            padding: 15px;
        }
      
        .ant-drawer-footer {
            padding: 0;
        }
    }

    .app-modal {
        .ant-modal {
            height: 100vh;
            width: 100% !important;
            max-width: 480px;
            margin: 0 auto;
      
            .ant-modal-header {
                background-color: map-get($colors, primary);
                border-bottom-width: 0px;
            }
      
            .ant-modal-content {
                height: 100vh;
                border-radius: 0px;
                background-color: map-get($colors, primary);
                
                .app-modal-content {
                  height: 90vh;
                }
            }
        }
    }

    .popup-modal {
        .ant-modal {
            width: 90% !important;
            max-width: 360px;
            margin: 0 auto;
            
            .ant-modal-header {
                background-color: map-get($colors, blank);
                border-bottom-width: 0px;
            }
      
            .ant-modal-content {
                border-radius: 0px;
                background-color: map-get($colors, blank);
                border-radius: 10px;
            }
        }
    }

    .card-shadow {
        box-shadow: 0px 2px 5px 0px alphaColor(black, 0.4);
    }

    .Mui-focused {
        color: map-get($colors, primary) !important;
    }
    
    .MuiInput-underline:after {
        border-bottom-color: map-get($colors, primary) !important;
        border-bottom-width: 1px !important;
    }
    
    .MuiInputAdornment-positionEnd {
        display: none;
    }

    input#carRef {
        height: 30px !important;
    }

    .dragger {
        width: 120px;
        height: 120px;
        border-radius: 50%;
        background-color: map-get($colors, secondary);

        img {
            width: 30px;
        }
    }

    .no-drag {
        pointer-events: none
    }

    .qr-code-scanner {
        & > section {
            height: calc(100vh - 50px);
            background-size: cover;

            & > section {
                height: calc(100vh - 50px);
                height: -webkit-fill-available;
                padding-top: initial !important;
                display: flex;
                align-self: center;
                align-content: center;
                -webkit-box-align: center;
                align-items: center;

                > div:first-child {
                    width: 60vw !important;
                    max-width: 320px;
                    height: 60vw !important;
                    max-height: 320px;
                    position: initial !important;
                    margin: 0 auto;
                    border: none !important;
                    box-shadow: none !important;
                }
            }
        }
    }

    a[title="Hosted on free web hosting 000webhost.com. Host your own website for FREE."] {
        display: none;
    }
}