/* Font Family */
* {
  font-family: Rubik,"\5FAE\8EDF\6B63\9ED1\9AD4",Microsoft JhengHei,Arial,Helvetica,-apple-system,BlinkMacSystemFont,Segoe UI,Helvetica Neue,Noto Sans,sans-serif,Heiti TC,PMingLiU,MingLiU,"\65B0\7D30\660E\9AD4","\7D30\660E\9AD4" !important;
}

h1, h2, h3, h4, h5, h6 {
  margin-block-start: 0px;
  margin-block-end: 0px;
}

/* Font Styling */
.break-all {
  word-break: break-all;
}

.text-shadow {
  text-shadow: 0 2px 4px alphaColor(black, 0.5);
}

.skip-word {
  overflow-x: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  min-width: 0;
}

.delete-line {
  text-decoration:line-through;
}

.underline {
  text-decoration: underline;
}

.pointer {
  cursor: pointer;
}

/* Font Size */
$fonts: (
  header: 18px,
  title: 24px,
  subTitle: 16px,
  body: 14px,
  desc: 12px,
  icon: 22px,
  large: 30px,
);

.uppercase {
  text-transform: uppercase;
}

.lowercase {
  text-transform: lowercase;
}

.capitalize {
  text-transform: capitalize;
}

@each $name, $font in $fonts {
  .small {
    .#{$name}-text {
      font-size: calc(#{$font} * 0.9) !important;

      * {
        font-size: calc(#{$font} * 0.9) !important;
      }
    }
  }

  .#{$name}-text {
    font-size: $font !important;
    line-height: calc(#{$font} * 1.4) !important;

    * {
      font-size: $font !important;
      line-height: calc(#{$font} * 1.4) !important;
    }
  }
}

/* Vertical Alignment */
$v-aligns: (top, center, bottom);
@each $v-align in $v-aligns {
  .vertical-#{$v-align} {
    vertical-align: #{$v-align} !important;
  }
}

/* Font Weight */
$weights: (bold, bolder, normal, lighter);
@each $weight in $weights {
  .#{$weight} {
    font-weight: #{$weight} !important;
  }
}

/* Font Align */
$aligns: (left, right, center, justify);
@each $align in $aligns {
  .text-#{$align} {
    text-align: #{$align} !important;

    input {
      text-align: #{$align} !important;
    }
  }
}
